window.addEventListener('DOMContentLoaded', () => {
    gsap.registerPlugin(ScrollTrigger);
    if(document.querySelector(".index-page")){
        teethAnim()
    }

    if(document.querySelector(".blog-card")){
        blogCardsAnimation()
    }

    if(document.querySelector(".gallery-card")){
        galleryCardsAnimation()
    }

    if(document.querySelector("._fade")){
        fadeAnimation()
    }

    // gradientAnim()
})


function teethAnim() {
    const tlTeeth = gsap.timeline({
        scrollTrigger: {
            trigger: '.teeth__img',
            start: '-=20%',
            end: '-=20%',
            scrub: 1
        }
    })
    tlTeeth.to('.teeth__img2', {
        yPercent: -38,
    })

    // gsap.from('.main', {
    //     backgroundImage:
    //         "radial-gradient(circle at 50% 0, hsla(180, 100%, 30%, 1) 0%, hsla(180, 100%, 30%, 0.5) 50%, hsla(180, 100%, 30%, 0) 100%), radial-gradient(circle at 6.7% 75%, hsla(245, 58%, 57%, 1) 0%, hsla(245, 58%, 57%, 0.5) 50%, hsla(245, 58%, 57%, 0) 100%), radial-gradient(circle at 93.3% 75%, hsla(100, 58%, 52%, 1) 0%, hsla(100, 58%, 52%, 0.5) 50%, hsla(100, 58%, 52%, 0) 100%)",
    //     repeat: -1,
    //     yoyo: "true",
    //     ease: "none",
    //     duration: 14
    // });
}

function blogCardsAnimation() {
    const blogCards = Array.from(document.querySelectorAll('.blog-card'));

    blogCards.forEach(card => {
        cardAnimation(card)
    })
}

function galleryCardsAnimation() {
    const cards = Array.from(document.querySelectorAll('.gallery-card'));

    cards.forEach(card => {
        cardAnimation(card)
    })
}

function fadeAnimation() {
    const fadeItems = Array.from(document.querySelectorAll('._fade'));

    fadeItems.forEach(card => {
        cardAnimation(card)
    })
}


function cardAnimation(card) {
    gsap.from(card, {
        yPercent: 40,
        opacity: .25,
        scrollTrigger: {
            trigger: card,
            start: 'top bottom',
            toggleActions: 'play none none reverse'
        }
    })
}

function gradientAnim() {
    var el = document.querySelector('._linear-bg');

    var gradValsNow;
    var rippleAnim;
    var rippleSize = 20;
    var animLengthSecs = 30;
    var radGradShape = 'circle';
    var radGradPos = 'top';
    var animEase = Power1.easeNone;
// var animEase = Power1.easeOut;
// var animEase = Back.easeOut.config(1.7);
// var animEase = Elastic.easeOut.config(1, 0.3);
// var animEase = Bounce.easeOut;
// var animEase = RoughEase.ease.config({ template:  Power0.easeNone, strength: 1, points: 20, taper: "none", randomize:  true, clamp: false});
// var animEase = SlowMo.ease.config(0.7, 0.7, false);
// var animEase = SteppedEase.config(60);
// var animEase = Circ.easeOut;
//var animEase = Expo.easeOut;

    function initGrad() {
        gradValsNow = {
            col1: 'hsla(253, 88%, 60%, .8)',
            col2: 'hsla(253, 88%, 61%, .9)',
            col1Pos: -10,
            col2Pos: rippleSize
        };
        runRipple();
    }

    function runRipple() {
        if (rippleAnim) rippleAnim.kill();

        rippleAnim = TweenLite.to(
            gradValsNow, // object whose properties will be tweened
            animLengthSecs,
            {
                col1Pos:            100,
                col2Pos:            100 + rippleSize,
                onUpdate:           paintGradVals,
                onUpdateParams:     [el],
                ease:               animEase,
                onComplete:         initGrad
            }
        );
    }

    function paintGradVals(el) {

        TweenLite.set(
            el, { backgroundImage: 'repeating-radial-gradient(' + radGradShape + ' farthest-side at ' + radGradPos + ', ' + gradValsNow.col1 + ' ' + gradValsNow.col1Pos + '%, ' + gradValsNow.col2 + ' ' + gradValsNow.col2Pos + '%)' }
        );

    }

    initGrad();
}
